

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    background: rgb(60, 60, 60);
}

.app {
    margin: 12em auto;
    display: flex;
    padding: 1.3em;
    justify-content: space-between;
    max-width: 51em;
    height: 30em;
    border-radius: 0.5em;
    border: 1px solid rgb(223, 223, 223);
    background: rgb(255, 255, 255);

    @media (max-width: 600px) {
        padding: 1.5em 1em 1em 1.5em;
        flex-direction: column;
        margin: 0 0;
        max-width:100%;
        height: 100vh;
    }

    &__infos {
        // background: blueviolet;
        display: flex;
        flex-direction: column;
        width: 25em;

        @media (max-width: 600px) {
            width: 100%;
        }
    }

    &__title {
        @media (max-width: 600px) {
            margin-bottom: 1em;
        }
    }

    input {
        border: 0;
        outline: 0;
        border-radius: .5em;
        border: 1px solid rgb(206, 206, 206);
    }

    &__input__top {
        margin-bottom: 1.2em;
        padding: .9em 0.8em;

        @media (max-width: 600px) {
            width: 85%;
        }
    }

    &__inputs {
        display: flex;
        flex-wrap: wrap;
    }

    &__input__bottom {
        margin-bottom: 1.95em;
        margin-right: 0.5em;
        padding: .9em 0.4em;
        width: 45.50%;

        &:nth-child(2) {
            margin-right: 0;
        }

        @media (max-width: 600px) {
            width: 41%;
        }
    }

    &__send {
        padding: 1em .8em;
        width: 8em;
        color: rgb(228, 228, 228);
        font-weight: 900;

        border: 0;
        outline: 0;
        border-radius: .5em;
        border: 1px solid rgb(190, 190, 190);
        background: rgb(60, 60, 60);
        cursor: pointer;
    }

    &__list {
        padding: 1em;
        display: flex;
        flex-direction: column;
        // border: 1px solid rgb(238, 238, 238);
        width: 23em;
        list-style-type: none;
        overflow-y: scroll;

        &::-webkit-scrollbar {
            width: .4em;
        }

        &::-webkit-scrollbar-thumb {
            background: rgb(197, 197, 197);
            border-radius: .5em;
        }

        @media (max-width: 600px) {
            padding: .5em 0;
            width: 100%;
            height: 33em;

            &::-webkit-scrollbar {
                width: .8em;
            }
        }
    }

    &__item {
        padding: 1em 1.8em;
        background: rgb(251, 251, 251);
        border: 1px solid rgb(218, 218, 218);
        border-radius: 0.5em;

        @media (max-width: 600px) {
            width: 70%;
        }

        @media (max-width: 425px) {
            padding: 1em .85em;
            width: 90%;
        }

        @media (max-width: 320px) {
            width: 100%;
        }
    }

    .it {
        margin-bottom: 1em;

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__name {
        font-weight: 900;
        font-size: 1.1em;
        margin-bottom: .5em;
    }

    &__num {
        font-weight: 500;
    }

    .btn {
        margin-top: .9em;
        margin-right: 1em;
        padding: .65em 1em;
        width: 6.65em;
        border-radius: .5em;
        outline: 0;
        border: none;
        background: transparent;
        cursor: pointer;
        color: white;
        font-weight: 900;

        &:nth-child(1) {
            background: rgb(255, 112, 112);
        }

        &:nth-child(2) {
            background: rgb(121, 121, 255);
            width: 9em;
        }
    }

    &__btn__filter {
        margin-top: 2em;
        padding: .8em 1em;
        width: 11.3em;
        border-radius: .5em;
        outline: 0;
        border: 0;
        border: 0.05em solid #d8d8d8;
        cursor: pointer;
        font-weight: 700;
    }
}


.error {
    margin-top: 5em;

    &__message {
        padding: .7em 1em;
        border-radius: .5em;
        font-size: 1.18em;
        background: rgba(255, 0, 0, 0.825);
        color: white;
    }
}

.Success {
    margin-top: 6.3em;
    padding: .7em 1em;
    border-radius: .5em;
    font-size: 1.18em;
    background: rgb(14, 165, 14);
    color: white;

    @media (max-width: 711px) {
        margin-top: 4em;
    }
}